import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'app-faq',
  templateUrl: './faq.component.html'
})
export class FAQComponent implements OnInit {

  title = 'Preguntas frecuentes';

  faq = [
    {
      pregunta: 'Existe un limite de vehculos para ponerse a la venta por usuario?',
      respuesta: 'No existe limite de vehculos, pero es importante a resaltar que por cada vahculo que se venda el sitio recibir una comisin por haberlo promocionado.'
    },
    {
      pregunta: 'Es necesario comprar y vender vehculos cuando me registro?',
      respuesta: 'No, cuando te vuelves un usuario del sitio puedes, tanto comprar como vender vehculos, pero no estas obligado a realizar ambas.'
    },
    {
      pregunta: 'Existe un limite de tiempo para una publicacin de una venta?',
      respuesta: 'No, una vez que se realiza una publicacin esta persiste hasata que se lleva a cabo la compra del vehculo o el vendedor la desactiva.'
    },
    {
      pregunta: 'Hay un precio mximo para poner a la venta un vehculo?',
      respuesta: 'Como tal no existe dicho limite, se deja a la libertad del vendedor el precio al cual quiera dar a la venta su vehculo, pero es importante que evale bien dicho precio, pues es claro que un precio exagerado no parecera atractivo para los interesados en el vehculo.'
    },
    {
      pregunta: 'Existe otra opcin de envo del vehculo que no sea por parte de los proveedores del sitio web?',
      respuesta: 'Si hay otra opcin, la cual es, una vez realizada la compra, llegar a un acuerdo con el vendedor para poder determinar un punto de entrega del vehculo. En dicho caso, el sitio web se deslinda de cualquier situacin relacionada con la entrega del vehculo.'
    },
    {
      pregunta: 'Una vez hecha la compra de un vehculo cuanto tiempo tengo que esperar para su entrega?',
      respuesta: 'Si se eligi la opcin para entregar el vehculo por parte de los proveedores del sitio, cada uno de ellos tiene presupuestada cierta cantidad de das hbiles para la entrega (entre ms rpida sea la entrega mayor ser el costo), pero si se eligi acordar la entrega con el vendedor entonces el tiempo si puede variar demasiado dependiendo del acuerdo al que se llegue'
    }
  ];

  step = 0;

  setStep(index: number) {
    this.step = index;
  }

  nextStep() {
    this.step++;
  }

  prevStep() {
    this.step--;
  }

  constructor() {
  }

  ngOnInit() {
  }
}
